<template>
	<div class="zstp_fg_page1">
		<el-dialog :visible.sync="dialogVisible" width="80%">
			<div v-show="!showGrap" style="line-height: 100px;height: 100px;text-align: center;font-size: 20px;color: #b80000">
				<i class="am-icon-spinner am-icon-spin"></i> loading
			</div>
			<div v-show="showGrap=='暂无数据'">
				<img style="display:block;margin: 20px auto;" src="../../../assets/no-sj.png">
			</div>
	  		<div v-show="showGrap&&showGrap!='暂无数据'" id="graphCharts" style="margin:0 auto; width: 1200px;height: 700px;text-align: center;"></div>
		</el-dialog>
		<div class="cont box-border-shadow">
			<div class="list-btn fl" @click="showGraph()">
	            知<br/>识<br/>图<br/>谱
	        </div>
			<div style="overflow-y: auto;">
				<div v-if="!faguiList.length">
					<img style="display:block;margin: 50px auto;" src="../../../assets/no-sj.png">
				</div>
            	<div v-else-if="faguiList=='loading'" style="line-height: 100px;height: 100px;text-align: center;font-size: 20px;color: #b80000">
					<i class="am-icon-spinner am-icon-spin"></i> loading
				</div>
				<div v-else>
					<ul class="faguiList" v-show="steps==1">
		                <li v-for="listItem in faguiList" class="item">
		                    <p class="item-head">
		                    	<span class="icon-fl">法律法规</span> 
		                    	<span @click="selectFaguiItem(listItem)" class="title">
		                    		{{listItem|getText}}
			                    </span>
		                    </p>
		                </li>
	            	</ul>
	            	<div class="faguiXq" v-show="steps==2">
	            		<h1 style="color: #b80000;font-size: 34px;text-align: center;font-weight: bold;line-height: 44px">{{fgBiaoti}}</h1>
	            		<span  @click="steps=1" style="font-size: 18px;color: #b80000;line-height: 40px;cursor: pointer;"><i class="el-icon-d-arrow-left"></i> 返回上一步</span>
						<!-- <ul class="faguiList">
			                <li v-for="(item,index) in faguiXq" class="item">
			                    <p class="item-head">
			                    	<span class="icon-fl">
			                    		<el-button plain>段落实体抽取</el-button>
			                    	</span> 
			                    	<span @click="selectFaguiItem(listItem)" class="title">
			                    		{{listItem|getText}}
				                    </span>
			                    </p>
			                </li>
		            	</ul> -->
		            	<p class="duanluo" v-for="(item,index) in faguiXq" :id="item.md5_id" @click="getFauiJuZi(item)">
		            		<span @click="getFauiJuZi(item)" class="title">
	                    		段落{{index+1}}：{{item.text|getText}}
		                    </span>
	            			<span class="icon-fl"  @click="getDialog(item,1)">
	                    		<el-button plain>段落实体抽取</el-button>
	                    	</span>
	            		</p>
	            		<!-- <p class="duanluo" v-for="(item,index) in faguiXq" :id="item.md5_id" @click="getFauiJuZi(item)">
	            			段落{{index+1}}：{{item.text|getText}}
	            			<el-button plain>段落实体抽取</el-button>
	            		</p> -->
	            	</div>
	            	<div class="faguiDuanLuo" v-show="steps==3">
	            		<h1 style="color: #b80000;font-size: 34px;text-align: center;font-weight: bold;line-height: 44px">{{fgBiaoti}}</h1>
	            		<span  @click="steps=2" style="font-size: 18px;color: #b80000;line-height: 40px;cursor: pointer;"><i class="el-icon-d-arrow-left"></i> 返回上一步</span>
	            		<p style="color: #b80000;text-indent: 40px;font-size: 18px;line-height: 30px">段落：{{fgDuanluo|getText}}</p>
	            		<p class="duanluo" v-for="(item,index) in faguiJuZi" @click="getDialog(item,2)">
	            			语句{{index+1}}：{{item.text|getText}}
	            		</p>
	            	</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import * as echarts from 'echarts'
	import reqData from "./../../../reqData.js"
	import './../image/jquery.easyui.min.js'
	import amazeui from 'amazeui'	
	// import Cropper from 'cropperjs'
	// import 'cropper/dist/cropper.css'
	export default{
		name:'zstp_fg_page1',
		data(){
			return {
				faguiList:[],
			    faguiXq:[],
			    faguiJuZi:[],
			    steps:1,
			    fgBiaoti:'',
			    fgDuanluo:'',
			    fgJuZi:'',
			    dialogFormVisible: false,
		        form1: {
		          word1:'',
		          word2:'',
		          relation:''
		        },
		        form2: {
		          word1:'',
		          word2:'',
		          relation:''
		        },
		        form3: {
		          word1:'',
		          word2:'',
		          relation:''
		        },
		        formLabelWidth: '120px',
		        radio: '1',
		        lishipingjia:{},
		        yd1:true,
		        radio1:[],
			    ydText1:'',
			    getQuestionData:[],
			    dialogVisible:false,
			    showGrap:false
			}
		},
		components:{
		},
		computed:{
			company(){
				return JSON.parse(sessionStorage.getItem("company"));
			},
			userName(){
				if(sessionStorage.getItem('username')){
					return sessionStorage.getItem('username');
				}
			},
		},
		filters:{
	    	getText(value){
	    		if(value){
		    		return value.trim();
	    		}
	    	}
	    },
		methods:{
			unique(arr){
	   　　　 	var newArr = [arr[0].name];
		   		var data=[arr[0]];
		   		//去重复项
		  　　 	for(var i=1;i<arr.length;i++){
		　　　　		if(newArr.indexOf(arr[i].name) == -1){
		          　　  newArr.push(arr[i].name);
		          	    data.push(arr[i]);
		    　　  	}
	    		}
	    		//区分值和主题
	    		for(var i=0;i<data.length;i++){
	    		 	if(data[i].des&&data[i].des==='1'){
	    		 		data[i].category="业务概念实体";
	    		 		data[i].itemStyle={
				             normal: {
				                 color: '#ff756e',
				                 borderColor:'#e06760',
				                 borderWidth:2,
				             }
				         }
	    		 	}else if(data[i].des&&data[i].des==='2'){
	    		 		data[i].category="财务审计概念实体";
	    		 		data[i].itemStyle={
				             normal: {
				                 color: '#68bdf6',
				                 borderColor:'#5ca8db',
				                 borderWidth:2,
				             }
				         }
	    		 	}else if(data[i].des&&data[i].des==='3'){
	    		 		data[i].category="组织实体";
	    		 		data[i].itemStyle={
				             normal: {
				                 color: '#ffd86e',
				                 borderColor:'#edba39',
				                 borderWidth:2,
				             }
				         }
	    		 	}else if(data[i].des&&data[i].des==='4'){
	    		 		data[i].category="审计对象实体";
	    		 		data[i].itemStyle={
				             normal: {
				                 color: '#6dce9e',
				                 borderColor:'#60b58b',
				                 borderWidth:2,
				             }
				         }
	    		 	}else if(data[i].des&&data[i].des==='5'){
	    		 		data[i].category="其他";
	    		 		data[i].itemStyle={
				             normal: {
				                 color: '#fb95af',
				                 borderColor:'#e3869d',
				                 borderWidth:2,
				             }
				         }
	    		 	}
	    		}
	    		console.log(data);
		        return data;
		    },
			graphOption(nodes,links){
		    	console.log(nodes);
		    	console.log(links)
		    	let option = {
				     title: {
				         text: ''
				     },
				     tooltip: {
				         formatter: function(x) {
				             return x.data.name;
				         }
				     },
				     legend: {
				        x: "center",
				        data: ["业务概念实体", "财务审计概念实体", '组织实体','审计对象实体','其他']
				    },
				     series: [{
				         type: 'graph',
				         layout: 'force',
				         focusNodeAdjacency: true,
				         symbolSize: 60,
				         roam: true,
				         categories: [{
			                name: '业务概念实体',
			                itemStyle: {
				                 color: '#ff756e',
				                 borderColor:'#e06760',
				                 borderWidth:2,
				            }
			             }, {
			                name: '财务审计概念实体',
			                itemStyle: {
				                 color: '#68bdf6',
				                 borderColor:'#5ca8db',
				                 borderWidth:2,
				            }
			             }, {
			                name: '组织实体',
			                itemStyle: {
				                 color: '#ffd86e',
				                 borderColor:'#edba39',
				                 borderWidth:2,
				            }
			             }, {
			                name: '审计对象实体',
			                itemStyle: {
				                 color: '#6dce9e',
				                 borderColor:'#60b58b',
				                 borderWidth:2,
				            }
			             },
			             {
			                name: '其他',
			                itemStyle: {
				                 color: '#fb95af',
				                 borderColor:'#e3869d',
				                 borderWidth:2,
				            }
			             }],
				         edgeSymbol: ['none', 'arrow'],
				         // edgeSymbolSize: [80, 10],
				         hoverAnimation:true,
				         force: {
				         	initLayout:'circular',
				             repulsion: 1500,
            				 layoutAnimation:false,
            				 gravity:0.5,
				             edgeLength: [16, 30],
				             layoutAnimation:true
				         },
				         draggable: true,
						 // symbol:'diamond',
				         // itemStyle: {
				         //     normal: {
				         //         color: '#ff756e',
				         //         borderColor:'#e06760',
				         //         borderWidth:2,
				         //     },

				         // },
				         lineStyle: {
				             normal: {
				                 width: 1,
				                 color: '#000',
				                 curveness:0.2
				             }
				         },
				         edgeLabel: {
				             normal: {
				                 show: true,
				                 textStyle: {fontSize: 18,color:'#000'},
				                 formatter: function(x) {
				                 	let name=x.data.name;
						         	if(name.length>5){
										name=name.substr(0,5)+'...';
										// v.nameSub=name;
									}else{
										name=name.substr(0,5);
										// v.nameSub=name;
									}
				                     return name;
				                 }
				             }
				         },
				         label: {
				         	show:true,
				         	textStyle:{
				         		fontSize:18
				         	},
				            formatter: function(x) {
			                 	let name=x.data.name;
					         	if(name.length>3){
									name=name.substr(0,3)+'...';
									// v.nameSub=name;
								}else{
									name=name.substr(0,3);
									// v.nameSub=name;
								}
			                     return '{a|'+name+'}';
			                },
			                rich: {
						        a: {
						            color: '#fff',
						            fontSize: 18,
						        },
						    }
				         },
				        nodes: nodes, 
						links:links 
				    }]
				};
				return option;
		    },
			showGraph(){
				let _this=this;
				_this.showGrap=false;
				_this.dialogVisible=true;
				reqData
			        .req({
			          apiName: "shitiguanxichouqu_shitiguanxi_zhishitupu",
			        })
			        .then(res => {
			          res = res.data;
			          if (res && res.code === "200") {
			            let data = res.data;
			            console.log(data);
			            let nodes=data.node
						let links=data.links;
						if(nodes.length){
							_this.showGrap=true;
							nodes=_this.unique(nodes);
							console.log(nodes);
							// links=this.linksFormat(links);
							
							var graphCharts = echarts.init(document.getElementById('graphCharts'));
				 	 // 		graphCharts.showLoading();
							// graphCharts.hideLoading();
					    	graphCharts.setOption(_this.graphOption(nodes,links)); 
				    	    $(window).resize(function(){
								graphCharts.resize({
									width:'auto',
									height:'auto'
								})
						    })
						}else{
							_this.showGrap="暂无数据";
						}    
			          }
			        })
			        .catch(err => {
			          console.log(err);
			       });
			},
			getQuestion1(){
				reqData
			        .req({
			          apiName: "showQues",
			          data: {   
			            qiye_id: "shitichouqu",          
			            yidian_name: this.fgJuZi.juzi_md5_id,
			            feilei_id:"shitichouqu"
			          }
			        })
			        .then(res => {
			          res = res.data;
			          if (res && res.code === "200") {
			            let data = res.data;
			            console.log(data);
			            this.getQuestionData=data;    
			          }
			        })
			        .catch(err => {
			          console.log(err);
			       });
			},
			appraise1(index){
				if(this.radio1[index]){
					let n=this.radio1[index].split("-")[0];
					let id=this.radio1[index].split("-")[1];
					console.log(n+"====="+id);
					 reqData
				        .req({
				          apiName: "appraiseQues",
				          data: {             
				            // qiye_id: this.getWord,
				            // feilei_id:'yd-'+this.id,
				            yidian_id:id,
				            num:n,
				            username:this.userName
				          }
				        })
				        .then(res => {
				          res = res.data;
				          if (res && res.code === "200") {
				            let data = res.data;
				            alert("评价成功");
				            this.radio1=[];
				          }
				        })
				        .catch(err => {
				          console.log(err);
				       });
				}else{
					alert('请选择合理或不合理')
				}
			},
			addQues1(){
				reqData
				    .req({
				      apiName: "addQues",
				      data: {             
				      qiye_id: "shitichouqu",
				      yidian_name: this.fgJuZi.juzi_md5_id,
				      feilei_id:"shitichouqu",
				      username:this.userName,
				      yidian:this.ydText1
				    }
				    })
				    .then(res => {
				      res = res.data;
				      if (res && res.code === "200") {
				       let data = res.data;
				       alert("添加成功");
				       this.ydText1='';
				       this.yd1=true;
				       this.getQuestion1();
				    }
				    })
				    .catch(err => {
				       console.log(err);
				    });	
			},
		  	getFaguiList(){
				this.steps=1;
				this.faguiList="loading";
				reqData
				    .req({
				      apiName: "shitiguanxichouqu_faguibiaoti",
				    })
				    .then(res => {
				      res = res.data;
				      if (res && res.code === "200") {
				       let data = res.data;
				       console.log(data);
				       this.faguiList=data;
				    }
				    })
				    .catch(err => {
				       console.log(err);
				    });	
			},
			selectFaguiItem(data){
					// let text="<span style='color:#b80000'>"+this.input+"</span>";
					// data.text=data.text.replace(new RegExp(this.input, 'g'),text);
					this.fgBiaoti=data;
					reqData
					    .req({
					      apiName: "shitiguanxichouqu_faguiduanluo",
					      data:{
					      	biaoti:data
					      }
					    })
					    .then(res => {
					      res = res.data;
					      if (res && res.code === "200") {
					       let data = res.data;
					       console.log(data);
					       this.faguiXq=data;
					       this.steps=2;
					    }
					    })
					    .catch(err => {
					       console.log(err);
					    });
			},
			getFauiJuZi(item){
				this.fgDuanluo=item.text;
				reqData
				    .req({
				      apiName: "shitiguanxichouqu_faguijuzi",
				      data:{
				      	md5_id:item.md5_id
				      }
				    })
				    .then(res => {
				      res = res.data;
				      if (res && res.code === "200") {
				       let data = res.data;
				       console.log(data);
				       this.faguiJuZi=data;
				       this.steps=3;
				    }
				    })
				    .catch(err => {
				       console.log(err);
				    });
			},
			getDialog(item,index){
				if(this.userName){
					// this.$router.push({ name: 'zstp_fg_page2', params: { faguiJuZiData: item }})
					let text=item.text;
					let id=(index==1)?item.md5_id:item.juzi_md5_id
					// let md5_id=item.md5_id;
					let fgBiaoti=this.fgBiaoti;
					let routeData = this.$router.resolve({name:'zstp_fg_page2',query:{text,id,index,fgBiaoti}});
					console.log(routeData);
					window.open(routeData.href, '_blank');
					// this.fgJuZi=item;
					// this.dialogFormVisible=true;
					// console.log(item);
					// this.showPingLun(item.juzi_md5_id);
					// this.getQuestion1();
				}else{
					alert("请先登录");
				}
			},
			showPingLun(id){
				reqData
					.req({
				      apiName: "shitiguanxichouqu_shitiguanxi_chaxun",
				      data:{
				      	juzi_md5_id:id
				      }
				    })
				    .then(res => {
					    res = res.data;
					    if (res && res.code === "200") {
					    let data = res.data;
					    console.log(data);
					    this.lishipingjia=data;
					    }
				    })
				    .catch(err => {
				       console.log(err);
				    });
			},
			addPingLun(form,index){
				let word1=form.word1;
		    	let word2=form.word2;
		    	let relation=form.relation;
		    	let id=this.fgJuZi.juzi_md5_id;
		    	if(!word1){
		    		alert("请输入主体1");
		    	}else if(!word2){
					alert("请输入主体2");
		    	}else if(!relation){
					alert("请输入关系");
		    	}else{
		    		reqData
					    .req({
					      apiName: "shitiguanxichouqu_shitiguanxi",
					      data:{
					      	juzi_md5_id:id,
					      	shiti1:word1,
							shiti2:word2,
							guanxi:relation,
							biaoshi:index
					      }
					    })
					    .then(res => {
					      res = res.data;
					      if (res && res.code === "200") {
					       let data = res.data;
					       alert("添加成功");
					       this.showPingLun(id);
					    }
					    })
					    .catch(err => {
					       console.log(err);
					    });
		    	}
			}
		},
	    mounted(){
	    	this.getFaguiList();
	    }
	}
</script>
<style scoped="">
	.zstp_fg_page1 .cont{
		position: relative;
		width: 90%;
	    margin: 30px auto;
	    min-height: 600px;
	    padding: 40px;
	    box-sizing: border-box;
	} 
	.zstp_fg_page1 .cont .list-btn {
	    position: absolute;
	    top: 80px;
	    right: 0;
	    z-index: 9;
	    width: 32px;
	    text-align: center;
	    background-color: #c24b4b;
	    color: #fff;
	    text-shadow: 5px 2px 6px rgba(0,0,0,0.3);
	    font-size: 16px;
	    padding: 8px;
	    -webkit-box-sizing: border-box;
	    box-sizing: border-box;
	    border-radius: 3px;
	    cursor: pointer;
	}
	.zstp_fg_page1 .cont .faguiList .item{
	    padding: 20px 0;
	    border-bottom: 1px solid #e0e0e0;
	}
	.zstp_fg_page1 .cont .faguiList .item p{
	    height: 28px;
	    line-height: 28px;
	    color: #999999;
	}
	.zstp_fg_page1 .cont .faguiList .item .item-head{
	    margin-bottom: 14px;
	    cursor: pointer;
	    white-space: nowrap;
	    overflow: hidden;
	    text-overflow: ellipsis;
	}
	.zstp_fg_page1 .cont .faguiList .item .item-head .icon-fl{
	    padding: 7px 12px;
	    background-color: #f59536;
	    border-radius: 5px;
	    color: #fff;
	}
	.zstp_fg_page1 .cont .faguiList .item .item-head .title{
	    color: #333;
	    font-size: 18px;
	    /*font-weight: bold;*/
	}
	.zstp_fg_page1 .cont .faguiList .item .item-head .title:hover{
		color: #b80000;
	}
	.zstp_fg_page1 .cont .duanluo{
		font-size: 18px;
		line-height: 30px;
		text-indent: 40px;
		cursor: pointer;
	}
	.zstp_fg_page1 .cont .duanluo:hover{
		background-color: rgba(184,0,0,0.1);
	}
	.zstp_fg_page1 .cont .table_list td,
	.zstp_fg_page1 .cont .table_list th{
		border: 1px solid #ccc;
		text-align: center;
		line-height: 40px;
		width: 33%
	}
	.zstp_fg_page1 .cont .table_list .footer_tr td{
		border: none;
		line-height: 50px;
	}

	.zstp_fg_page1 .grid-content{
		padding: 20px;
	    -webkit-box-sizing: border-box;
	    box-sizing: border-box;
	    border: 1px solid #ccc;
	    border-radius: 6px;
	    margin-bottom: 20px;
	}


	.zstp_fg_page1 .modal-content{
        /*padding-left: 16px;*/
        box-sizing: border-box;
    }
    .zstp_fg_page1 .am-form-group {
        margin-bottom: 0;
    }
   .zstp_fg_page1 .modal-content .list li{
        padding-top: 13px;
        padding-bottom: 13px;
        border-bottom: 1px solid #ebebeb;
    }
    .zstp_fg_page1 .modal-content .list li .sub{
        color: #b80000;
        cursor: pointer;
        margin-right: 10px;
    }
    .zstp_fg_page1 .modal-content .btn-add{
        color: #b80000;
        cursor: pointer;
        height: 58px;
        line-height: 58px;
    }
    .zstp_fg_page1 .modal-content  .text-area{
        width: 100%;
        margin: 0 auto;
    }
    .zstp_fg_page1 .modal-content  .text-area .area{
        width: 100%;
        border: 1px solid #ebebeb;
        color: #333;
        padding: 10px;
        box-sizing: border-box;
        overflow-x: hidden;
        overflow-y: auto;
    }
    .zstp_fg_page1 .modal-content  .text-area button{
        outline: none;
        background-color: #b80000;
        border:none;
        padding: 6px 14px;
        margin-top: 10px;
        color: #fff;
        border-radius: 6px;
        cursor: pointer;
    }
</style>



